@import '../../../scss/variables.scss';

.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
  pointer-events: none;
  transition: 0.5s all;

  &__active {
    opacity: 1;
    pointer-events: all;
  }
}

.content {
  display: flex;
  padding: 1.5rem;
  border-radius: 1rem;
  background-color: $personCard;
  transform: scale(0.5);
  transition: 0.3s all;

  @media (max-width: 650px) {
    flex-direction: column;
    align-items: center;
  }

  &__active {
    transform: scale(1);
  }

  &__img {
    width: 20rem;
    height: 20rem;
    border-radius: 1rem;

    @media (max-width: 900px) {
      width: 15rem;
      height: 15rem;
    }

    @media (max-width: 400px) {
      width: 10rem;
      height: 10rem;
    }
  }

  &__description {
    display: flex;
  }

  &__list {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    @media (max-width: 650px) {
      align-items: center;
      gap: 0.5rem;
    }
  }
}
